/**
 * As we are using hash based navigation, hack fix
 * to highlight the current selected menu
 *
 * Requires jQuery
 */
function menuFix(slug: string, jQuery: Function) {
  var $ = jQuery;

  let menuRoot = $('#toplevel_page_' + slug);
  let currentUrl = window.location.href;
  let currentPath = currentUrl.substr( currentUrl.indexOf('admin.php') );

  menuRoot.on('click', 'a', function(this: any) {
    var self = $(this);

    $('ul.wp-submenu li', menuRoot).removeClass('current');

    if ( self.hasClass('wp-has-submenu') ) {
        $('li.wp-first-item', menuRoot).addClass('current');
    } else {
        self.parents('li').addClass('current');
    }
  });

  $('ul.wp-submenu a', menuRoot).each(function(index: number, el: any) {
    if ( $(el).attr( 'href' ) === currentPath ) {
      $(el).parent().addClass('current');
      return;
    }
  });
}

export default menuFix;
